@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.dashboard {
  width: 30%;
  margin: 0 auto;
  text-align: center;
  background-color: #19181A;
  box-shadow: 0px 0px 20px 8px #444444;
  color: #B19F9E;
}

.dashboard h1 {
  padding: 5px;
}


.householdMembers {
  width: 20vw;
  min-height: 50vh;
  background-color: #19181A;
  box-shadow: 0px 0px 20px 8px #444444;
  display: inline-block;
  margin-top: 20px;
}

.householdMembers h2 {
  text-align: center;
  color: #479761;
  padding: 20px;
}

.householdMembers ul {
  color: #B19F9E;
  padding: 20px;
}

.householdMembers li {
  list-style: none;
  padding: 10px;
  font-size: 17px;
}






.myChores {
  position: absolute;
  width: 50vw;
  min-height: 50vh;
  background-color: #19181A;
  box-shadow: 0px 0px 20px 8px #444444;
  display: inline-block;
  margin: 20px;
}

.myChores h2 {
  text-align: center;
  color: #CEBC81;
  padding: 20px;
}

.myChores .chore {
  margin: 10px auto;
  background-color: #202020;
  width: 80%;
  color: #B19F9E;
  box-shadow: 0px 0px 10px 3px #444444;
  transition: 0.3s;
  font-size: 20px;
}

.myChores .chore:hover {
  color: #CEBC81;
  box-shadow: 0px 0px 10px 3px #CEBC81;

}

.chore input {
  height: 20px;
  width: 20px;
}

.myChores button {
  box-shadow: 0px 0px 10px 3px #444444;
  display: block;
  margin: 20px auto;
  background-color: #202020;
  border: none;
  color: #B19F9E;
  padding: 15px 32px;
  text-align: center;
  font-size: 16px;
  transition: 0.3s;
}

.myChores button:hover {
  box-shadow: 0px 0px 10px 3px #CEBC81;
  color: #CEBC81;
}




.allChores {
  width: 25vw;
  position: absolute;
  margin-top: 20px;
  right: 20px;
  display: inline-block;
  min-height: 50vh;
  background-color: #19181A;
  box-shadow: 0px 0px 20px 8px #444444;

}

.allChores h2 {
  text-align: center;
  color: #A16E83;
  padding: 20px;
}

.allChores li {
  list-style: none;
  padding: 10px;
  font-size: 17px;
}

.allChores button {
  box-shadow: 0px 0px 10px 3px #444444;
  display: block;
  margin: 20px auto;
  background-color: #202020;
  border: none;
  color: #B19F9E;
  padding: 15px 32px;
  text-align: center;
  font-size: 16px;
  transition: 0.3s;
}

.allChores button:hover {
  box-shadow: 0px 0px 10px 3px #A16E83;
  color: #A16E83;
}


.member0 {
  color: skyblue;
}
.member1 {
  color: green;
}
.member2 {
  color: orange;
}
.member3 {
  color: pink;
}
.member4 {
  color: tan;
}

.addChore {
  color: #B19F9E;
  cursor: pointer;
}

.completed {
  text-decoration: line-through;
}

body {
  /* background-color: #202020; */
  background-color: #797979;
  font-family: 'Raleway', sans-serif;

}

/* Nav Bar */

header nav {
  height: 80px;
  background-color: #19181A;
  box-shadow: 0px 0px 20px 8px #444444;
}

header ul {
  list-style: none;
}

header li {
  display: inline;
  color: #B19F9E;
  line-height: 80px;
  font-size: 20px;
}

header a {
  color: #B19F9E;
  text-decoration: none;
}

.frontPage {
  color: #B19F9E;
  text-align: center;
  font-size: 100px;
  margin-top: 200px;
}

form {
  width: 60vw;
  margin: 200px auto;
  text-align: center;
  color: #B19F9E;
}

form .loginButton {
  display: inline-block;
  font-size: 25px;
  margin: 10px;
  cursor: pointer;
}

form input, form select {
  width: 250px;
}

